.head-foot-style, .pg-header, .pg-footer {
  position: relative;
  width: 100%;
  min-width: 720px;
  margin: 0;
  padding: 0.5rem 0;
  text-align: center;
  background-color: #101010;
  color: #00d700;
  font-family: "VT323", monospace; }

.vis-canvas, .stage .bg, .stage .gm {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0; }

.before-setup, .gm-container:before, input[type='range']:before, .ui-nav-btns .start-btn:hover:before, .ui-nav-btns .stop-btn:hover:before, .ui-nav-btns .step-btn:hover:before, .ui-save-reset .reset-btn:hover:before, .ui-save-reset .save-btn:hover:before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%; }

.slider-tick-styling, input[type='range'] {
  position: relative;
  width: 140px;
  cursor: pointer;
  background: linear-gradient(to right, transparent 3.5px, rgba(0, 0, 0, 0.7) 4.5px, grey 5.5px, grey 6.5px, white 6.5px, transparent 7.5px) repeat-x; }

.slider-track-styling, input[type='range']:before {
  height: 5px;
  margin-top: calc((18px - 5px) / 2);
  background: #202020;
  box-shadow: 1px 1px 1px rgba(255, 255, 255, 0.8), -0.5px -0.5px rgba(0, 0, 0, 0.6); }

.slider-thumb-styling, input[type='range']::-webkit-slider-thumb {
  background: repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 60%, rgba(255, 255, 255, 0.1) 75%), repeating-linear-gradient(left, transparent 0%, transparent 40%, rgba(0, 0, 0, 0.03) 45%), repeating-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 12%, rgba(255, 255, 255, 0.15) 22%), linear-gradient(#d2d2bc 0%, #ebebe0 47%, #d2d2bc 53%, #c2c2a3 100%);
  box-shadow: inset 2px 2px 3px 1px rgba(255, 255, 255, 0.6), 1px 1px 1px rgba(0, 0, 0, 0.5);
  position: relative;
  height: 18px;
  width: 9px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 2px; }
  .slider-thumb-styling:active, input[type='range']:active::-webkit-slider-thumb {
    padding: 0;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.8); }

/**
	*		Styling
	*/
html, body,
div, p, span,
button, select,
ul, ol,
h1, h2, h3, h4 {
  margin: 0;
  padding: 0; }

.pg {
