  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  min-width: 745px; }
  .pg-content {
    flex-grow: 1;
    width: 100%;
    text-align: center;
    background: radial-gradient(circle, rgba(60, 255, 140, 0.6), rgba(0, 0, 0, 0.9)); }
  .pg-footer > *:first-child {
    margin-right: 1rem; }
  .pg-footer a {
