  position: relative;
  margin: 2em 0;
  padding: 10px;
  border: 1px solid black;
  border-radius: 20px;
  text-align: center;
  user-select: none;
  box-shadow: inset 2px 2px 1px rgba(255, 255, 255, 0.8), inset 2px 0 2px 1px rgba(0, 0, 0, 0.5), inset -2px 0 2px 1px rgba(0, 0, 0, 0.5), 2px 2px 3px 1px rgba(0, 0, 0, 0.6); }
  .gm-container:before {
    background: linear-gradient(to right, rgba(77, 77, 77, 0.4), rgba(255, 255, 255, 0.4), rgba(77, 77, 77, 0.4));
    border-radius: 1rem; }
  .gm-container .generation {
    border-bottom: 1px solid white;
    border-right: 1px solid white;
